import React from 'react';
import {HashRouter as Router,Route,Switch} from 'react-router-dom';
import DashBoard from './DashBoard';
import CashIn from './CashIn';
import LazyLoad from './LazyLoad';
import CashOut from './CashOut';
import * as FaIcons from 'react-icons/fa';
import * as AiIcons from 'react-icons/ai';
import * as GiIcons from "react-icons/gi";
import { Link } from 'react-router-dom';
import '../css/homepage.scss';
import { IconContext } from 'react-icons';
import {connect} from 'react-redux';

/*
  Homepage is a funtion component to show the cashin, cashout and dashboard page
*/
class HomePage extends React.Component{
        constructor(props){
          super(props);
          this.state = {
            sidebar : false,
            username : '',
            showCashInSub : false,
            showCashOutSub : false
          }
        }
        render(){
          return(
            <div>
        <Router>
        <IconContext.Provider value={{ color: '#fff' }}>
        <div className='navbar'>
          <button className="logoutbutton" onClick={()=>{
            this.props.dispatch({
              type:"LOGOUT"
            });
            this.props.history.push("/")}
            }>Logout</button>
          <h1 className="welcomeuser">Welcome {this.props.username}</h1>
          <Link to='#' className='menu-bars'>
            <FaIcons.FaBars onClick={()=>{
              this.setState({sidebar : !this.state.sidebar,showCashInSub:false,showCashOutSub:false})
            }}/>
          </Link>
        </div>
        <nav className={this.state.sidebar ? 'nav-menu active' : 'nav-menu'}>
          <ul className='nav-menu-items' onClick={()=>{
            this.setState({sidebar : !this.state.sidebar})}
            }>
            <li className='navbar-toggle'>
              <Link to='#' className='menu-bars'>
                <AiIcons.AiOutlineClose />
              </Link>
            </li>
                <li key={0} className='nav-text'>
                  <Link to="/">
                  <AiIcons.AiFillHome />
                    <span>DashBoard</span>
                  </Link>
                </li>
                <li key={1} className='nav-text'>
                  <Link onClick={(e)=>{
                    e.stopPropagation();
                    this.setState({showCashInSub:!this.state.showCashInSub})}}> 
                  <GiIcons.GiPayMoney />
                    <span>CashIn</span>
                  </Link>
                </li>{this.state.showCashInSub?
                <div>
                <li key={3} className='nav-text-sub'>
                  <Link to="/cashin/new">
                    <span>New</span>
                  </Link>
                </li>
                <li key={4} className='nav-text-sub'>
                <Link to="/cashin/details">
                  <span>Details</span>
                </Link>
              </li></div>:<div></div>}
                <li key={5} className='nav-text'>
                  <Link onClick={(e)=>{
                    e.stopPropagation();
                    this.setState({showCashOutSub:!this.state.showCashOutSub})}}>
                  <GiIcons.GiReceiveMoney />,
                    <span>Cashout</span>
                  </Link>
                </li>{this.state.showCashOutSub?
                <div>
                <li key={6} className='nav-text-sub'>
                  <Link to="/cashout/new">
                    <span>New</span>
                  </Link>
                </li>
                <li key={7} className='nav-text-sub'>
                <Link to="/cashout/details">
                  <span>Details</span>
                </Link>
              </li></div>:<div></div>}
          </ul>
        </nav>
      </IconContext.Provider>
        <Switch>
          <Route path="/" exact component={DashBoard} />
          <Route path="/cashin/new" component={CashIn} />
          <Route path="/cashin/details" exact component={LazyLoad} />
          <Route path="/cashout/new" component={CashOut} />
          <Route path="/cashout/details" exact component={LazyLoad} />
        </Switch>
      </Router>
            </div>
          )
    }
  }

/*
  Exporting the homepage component such that any component can use this component by importing this component
*/
export default connect((state)=>{
  return{
    username : state.loginReducer.username
  }
})(HomePage);