import storage from 'redux-persist/lib/storage';
const defaultState = {
    username:'empty',
    password:'empty'
};
export default (state = defaultState,action) => {
    switch(action.type){
        case 'LOGOUT':
            console.log("logout");
            storage.removeItem('persist:root');
            state = defaultState;
        default:
            return state;
    }
};